import React from "react";
import LeftHeader from "./LeftHeader";
import ShowOnScroll from "./ShowOnScroll";

interface LanguagesProps {
    title: string;
    data: Array<{ language: string, level: string }>
}

let Languages = ({ title, data }: LanguagesProps) => {
    return (
        <div>
            <ShowOnScroll direction="left">
                <LeftHeader title={title} />
            </ShowOnScroll>

            <ul className="mt-1 text-left">
                {
                    data.map((d, i) =>
                        <ShowOnScroll key={i} direction="right">
                            <li className="mt-2 text-base font-sans font-semibold text-gray-600">
                                {d.language} - {d.level}
                            </li>
                        </ShowOnScroll>

                    )
                }
            </ul>
        </div>
    );
}

export default Languages;