import React from "react";


interface ImageAssetProps {
    img: string;
    x?: number;
    y?: number;
    rot?: number;
    size?: number;
    origin?: [string, string];
}

let ImageAsset = ({ img, x = 0, y = 0, rot = 0, size = 128, origin = ["top", "center"] }: ImageAssetProps) => {
    let baseStyle: React.CSSProperties = {
        zIndex: 0,
        position: 'fixed',
        top: `${y}px`,
        backgroundImage: `url(${img})`,
        backgroundSize: 'contain',
        backgroundRepeat: 'no-repeat',
        width: `${size}px`,
        height: `${size}px`,
        transform: `rotate(${rot}deg)`,
        transformOrigin: `${origin[0]} ${origin[1]}`,
    }

    switch (origin[1]) {
        default:
        case "center":
        case "left":
            baseStyle['left'] = `${x}px`;
            break;
        case "right":
            baseStyle['right'] = `${x}px`;
            break;
    }

    return (
        <div className="hidden a4:block" style={baseStyle} />
    );
}

export default ImageAsset;