import React, { useState } from 'react';
import { useRecoilState, useRecoilValue } from 'recoil';
import A4Page from './components/A4Page';
import Background from './components/Background';
import ColumnOne from './components/ColumnOne';
import ColumnTwo from './components/ColumnTwo';
import Description from './components/Description';
import ExperienceList from './components/ExperienceList';
import Footer from './components/Footer';
import Grid from './components/Grid';
import Modal from './components/Modal';
import OptionsContainer from './components/OptionsContainer';
import PersonalInfo from './components/PersonalInfo';
import ProfilePic from './components/ProfilePic';
import { currentLanguage, selectLanguange } from './components/RecoilState';
import Skills from './components/Skills';
import backImage from './img/madeira.png';
import laptop from './img/laptop.png';
import mug from './img/mug.png';
import ImageAsset from './components/ImageAsset';
import Languages from './components/Languages';


let App = () => {

  const [lang, setLang] = useRecoilState(selectLanguange);
  const [showModal, setShowModal] = useState(false);

  const onLanguageChange = (event: any) => {
    setLang(event.target.value);
  }

  const { personalInfoData, profile, educationData, experienceData, academicProjectsData, skillsData, languages, options, languagesSkills, aboutApp } = useRecoilValue(currentLanguage);

  return (
    <Background backImage={backImage}>

      <ImageAsset img={laptop} size={1024} rot={35} x={-850} y={200} origin={["top", "right"]} />
      <ImageAsset img={mug} size={200} rot={100} x={150} y={630} />
      <A4Page pageNumber={1} marginTop={true}>
        <Grid>
          <ColumnOne>
            <ProfilePic />
            <PersonalInfo {...personalInfoData} />
            <Skills {...skillsData} />
          </ColumnOne>

          <ColumnTwo>
            <ProfilePic onlyMobile={true} />
            <Description {...profile} />
            <ExperienceList {...experienceData} />
          </ColumnTwo>
        </Grid>
      </A4Page>


      <A4Page pageNumber={2}>
        <Grid>
          <ColumnOne>
            <Languages title={languagesSkills.title} data={languagesSkills.data} />
          </ColumnOne>
          <ColumnTwo>
            <ExperienceList {...educationData} />
            <ExperienceList {...academicProjectsData} start={0} end={3} />
          </ColumnTwo>
        </Grid>
      </A4Page>


      <A4Page pageNumber={3}>
        <Grid>
          <ColumnOne>
          </ColumnOne>
          <ColumnTwo>
            <ExperienceList {...academicProjectsData} start={3} />
          </ColumnTwo>
        </Grid>
      </A4Page>

      <Footer>
        <PersonalInfo {...personalInfoData} />
      </Footer>

      <Modal isShow={showModal} setModal={() => setShowModal(false)}>
        <h1 className="text-gray-800 text-2xl font-bold mt-8 uppercase mb-5">{aboutApp.title}</h1>
        <p className="text-lg font-sans font-semibold text-gray-800 mb-5">{aboutApp.subTitle}</p>
        <ul className="list-disc ml-4 text-base font-sans font-semibold text-gray-600">
          {
            aboutApp.data.map((d, i) =>
              <li key={i}>{d}</li>
            )
          }
        </ul>
      </Modal>
      <OptionsContainer>

        <button className="w-full bg-gray-50 shadow-xl hover:bg-gray-100 hover:text-gray-800 p-2 rounded-lg text-gray-900 border-2 border-red-400 text-base flex flex-col justify-items-center items-center mb-4" onClick={() => setShowModal(true)}>
          <span className="self-center">
            {options[0].value}
          </span>
        </button>

        <select className="appearance-none block w-full py-2 px-3 rounded-lg text-base  bg-gray-50 shadow-xl hover:bg-gray-200 hover:text-red-400 text-gray-900 border-2 border-red-400 focus:outline-none"
          value={lang} onChange={onLanguageChange}>
          {
            languages.map(v => (
              <option key={v.abr} className="appearance-none bg-gray-50 hover:bg-gray-200 " value={v.abr}>{v.translation}</option>
            ))
          }
        </select>
      </OptionsContainer>


    </Background>
  );
}

export default App;
