import React from "react";

interface ColumnOneProps {
    children?: React.ReactNode;
}

let ColumnOne = ({ children }: ColumnOneProps) => {
    return (
        <div className="hidden bg-gray-300 p-3 pt-8 a4:flex a4:flex-col  a4:col-span-4 print:flex print:flex-col print:col-span-4 rounded-xl overflow-hidden">
            <div className="a4:mb-3 print:mb-3">
                {children}
            </div>
        </div>
    )
}

export default ColumnOne;