import React from "react";

interface A4PageProps {
    pageNumber?: number;
    children?: React.ReactNode;
    marginTop?: boolean;
}

let A4Page = ({ pageNumber, children, marginTop }: A4PageProps) => {
    return (
        <div className={`w-full relative mt-0 p-4 a4:p-3 print:p-3 bg-gray-50 print:w-a4 a4:w-a4 a4:h-a4 print:h-a4 print:m-0 a4:mb-4 a4:shadow-2xl ${marginTop ? 'a4:mt-4' : ''}`}>
            {children}
            <div className="hidden a4:block print:block absolute right-3 bottom-3 text-base text-gray-800 font-semibold">{pageNumber}</div>
        </div >
    );
}

export default A4Page;