import { faEllipsisH, faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";
import './OptionsContainer.css';

interface OptionsContainerProps {
    children?: React.ReactNode;
}

let OptionsContainer = ({ children }: OptionsContainerProps) => {

    const [optionState, setOptionState] = useState(false);

    let handleState = () => {
        setOptionState(!optionState);
    }

    return (
        <div className={`print:hidden fixed origin-bottom-right bottom-5 right-5 a4:bottom-10 a4:right-10 bg-white shadow-lg p-4 ${optionState ? 'open-state' : ' close-state'} `}>

            <div className={`${optionState ? 'open-state-child ' : 'close-state-child'} flex flex-col justify-center items-end`}>
                <button className="origin-top-right top-0 right-0 bg-red-400 text-gray-50 hover:bg-red-600 hover:text-gray-50 p-3 w-5 h-5 flex justify-center items-center rounded-lg shadow-lg cursor-pointer mb-4 " onClick={handleState}>
                    <FontAwesomeIcon icon={faTimes} />
                </button>
                {children}
            </div>

            <button className={`${!optionState ? 'inverse-open-state-child ' : 'inverse-close-state-child pointer-events-none'} absolute origin-center top-0 left-0 flex flex-col justify-center items-center rounded-full w-12 h-12 bg-grey-50 text-gray-500 hover:bg-red-500 hover:text-gray-50 border-2 border-red-400 shadow-lg z-20`} onClick={handleState}>
                <FontAwesomeIcon icon={faEllipsisH} />
            </button>

        </div>
    )
}

export default OptionsContainer;